<template>
  <v-app>
    <v-content>
      <v-container id='main' fluid>
        <!-- Header -->
        <!-- Main -->
        <!-- Aside -->
          <!-- <iframe id="ytplayer" type="text/html" width="720" height="405"
            :src='src'
            frameborder="0" allowfullscreen></iframe> -->

        <v-navigation-drawer temporary v-model="drawer"
        width='500' light absolute>
          <v-list class="pt-0" dense>
              <v-divider></v-divider>
              <v-list-tile v-for="item in channelData" :key="item.id.videoId"
              @click="itemSelected(item)">
                <v-list-tile-action>
                  <!-- <v-icon>{{ item.snippet.thumbnails.default }}</v-icon> -->
                </v-list-tile-action>
                <v-list-tile-content>
                  <v-list-tile-title>{{ item.snippet.title }}
                  </v-list-tile-title>
                </v-list-tile-content>
              </v-list-tile>
          </v-list>
        </v-navigation-drawer>

        <v-btn @click='drawer = !drawer' fab color='purple'>
          <v-icon >menu</v-icon>
        </v-btn>

        <br><br>

        <!-- <div id='player'> -->
          <iframe id="ytplayer" type="text/html" width="720" height="405"
          :src='src' frameborder="0" allowfullscreen></iframe>
        <!-- </div> -->

      </v-container>
    </v-content>
  </v-app>
</template>

<script>
  import axios from 'axios'
  export default {
    data () {
      return {
        channelData: [],
        embed: 'https://www.youtube.com/embed/',
        src: '',

        drawer: false,
        items: [
          { title: 'Home', icon: 'dashboard' },
          { title: 'About', icon: 'question_answer' }
        ]
      }
    },
    created () {
      axios('https://rachel-jun-channel.herokuapp.com/')
        .then(res => {
          this.src = this.embed + res.data[0].id.videoId
          return this.channelData = res.data
        })
    },
    methods: {
      itemSelected(item) {
        this.src = this.embed + item.id.videoId
        this.drawer = false
      }
    }
  }
</script>

<style scoped>
  #ytplayer {
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-right: -50%;
    transform: translate(-50%, -50%);
  }
</style>



